body {
  font: 14px "Century Gothic", Futura, sans-serif;
  background-color: black;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin: 40px 0 40px 10%;
  font-size: 40px;
  color: lightblue;
}

.square {
  background: lightblue;
  border: 1px solid black;
  float: left;
  font-size: 60px;
  font-weight: bold;
  line-height: 34px;
  height: 150px;
  margin-right: 0px;
  margin-top: 0px;
  padding: 0;
  text-align: center;
  width: 150px;
}
.board {
  margin-left: 30%;
}
.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: lightblue;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
@media only screen and (max-width: 600px) {
  .square {
    background: lightblue;
    border: 1px solid black;
    float: left;
    font-size: 20px;
    font-weight: bold;
    line-height: 34px;
    height: 60px;
    margin-right: 0px;
    margin-top: 0px;
    padding: 0;
    text-align: center;
    width: 60px;
  }
  .status {
    margin: 90px 0 90px 0;
    font-size: 30px;
    color: lightblue;
  }
  .board {
    margin-left: 25%;
  }
  .ref {
    text-align: center;
    margin: 90px 0 0px 0;
  }
  .but {
    border-radius: 30px;
    height: 50px;
    border: none;
  }
}
.ref {
  text-align: center;
  margin: 10px 10px 10px 10px;
}
.but {
  border-radius: 30px;
  height: 50px;
  border: none;
}
